.footer {
    width: 100%;
    padding: 6rem 0; 
    background-color: rgb(19, 19, 19);
}

.footer-container{
    max-width: 1040px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction:column;
    padding:1rem;
}